@mixin profilePicMobileDynamics($height,$width){
  height: $height !important;
  width: $width !important;
}

@mixin customFontAttribute($fontSize){
  font-size: $fontSize !important;
}

.profilePicMobile{
  @include profilePicMobileDynamics(50px,50px);
  margin: 10px auto -100px;
}
.profileNameMobile{
  font-size: 12px;
  margin-top: 30px;
  margin-left: 35%;
  text-transform: uppercase;
}

.ProfileNameCard{
  border-radius: 20px !important;
  padding: 5px;
}

.profilePicDialog{
  @include profilePicMobileDynamics(200px,200px);
}
.customHeader{
  box-shadow: none !important;
  background-color: #fff !important;
  border-bottom: 1px dashed;
}
.customFontAttribute{
  @include customFontAttribute(15px);
}
.exampleContainer{
  margin: 0 auto;
  padding: 15px;
  display: table;
  .exampleBtn{
    margin: 10px;
  }
}