@mixin fontAttributes($fontColor){
  color: $fontColor;
}


.font{
  &-facebook{
    @include fontAttributes(#4267b2);
  }
  &-twitter{
    @include fontAttributes(#1da1f2);
  }
  &-instagram{
    @include fontAttributes(#231f20);
  }
  &-github{
    @include fontAttributes(#24292e);
  }
  &-stack-overflow{
    @include fontAttributes(#f48024);
  }
  &-linkedin{
    @include fontAttributes(#0077b5);
  }
}