@mixin LifeChapterNamesDynamics($margin,$fontSize){
  margin: $margin;
  text-transform: uppercase;
  font-size: $fontSize;
}

.TabContainer{
  padding: 10px;
}
.LifeChapterContainer{
  display: flex;
}
.LifeChapterImages{
  height: 50px;
  width: 50px;
}



.LifeChapterMobileNames{
  @include LifeChapterNamesDynamics(15px,8px);
}

.LifeChapterTabletNames{
  @include LifeChapterNamesDynamics(8px,15px);
}